body {
  margin: 0;
  padding: 0;
  font-family: sans-serif; }

.leaflet-container {
  height: 100%;
  width: 100%; }

.leaflet-div-icon {
  background: transparent;
  border: none; }

.container-block {
  display: flex;
  flex-flow: column;
  border-right: solid 3px #ddd;
  border-bottom: solid 3px #ddd;
  box-shadow: 1px 1px 2px 2px rgba(200, 200, 200, 0.4);
  border-radius: 2px;
  background-color: #f0f0f0;
  height: inherit;
  width: inherit; }

.container-selected {
  animation-duration: 1s;
  animation-name: glow;
  animation-iteration-count: infinite;
  animation-direction: alternate; }

@keyframes glow {
  from {
    box-shadow: 0 0 1px 1px rgba(200, 200, 200, 0.7); }
  to {
    box-shadow: 0 0 4px 4px rgba(200, 200, 200, 0.4); } }

.handle {
  cursor: grab;
  color: #557;
  min-height: 20px;
  border-bottom: solid 1px #ddd;
  display: flex;
  flex-direction: row-reverse; }
  .handle .blockTitle {
    width: 100%;
    overflow: hidden;
    font-size: 1em; }

.workArea {
  position: absolute;
  height: 100%;
  width: 100%; }

g.hover path {
  shape-rendering: geometricPrecision;
  stroke-width: 2; }

.block-content {
  color: #666;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden; }

.port {
  height: 15px;
  width: 15px;
  background: #eee;
  position: absolute;
  border: 1px solid #ddd;
  border-radius: 2px; }
  .port-input {
    top: calc(50% - 8px);
    left: -17px; }
  .port:hover {
    border: 1px solid #ccc;
    cursor: pointer; }
